




































import CubePage from '@/components/cube/cube-page.vue'
import { getArrayObjIndexByKey } from "@/utils";
import { FOLLOWERS_DATA, RECOMMEND_DATA, HOT_DATA } from '@/data/tab-bar.ts';
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {CubePage,},
})
export default class ViewComponent extends Vue {
  /**data */
  selectedLabel = '推荐'
  disabled = false
  tabLabels = [{
    label: '关注'
  }, {
    label: '推荐'
  }, {
    label: '热榜'
  }]
  loop = false
  autoPlay = false
  showDots = false
  slideOptions = {
    listenScroll: true,
    probeType: 3,
    /* lock y-direction when scrolling horizontally and  vertically at the same time */
    directionLockThreshold: 0
  }
  scrollOptions = {
    /* lock x-direction when scrolling horizontally and  vertically at the same time */
    directionLockThreshold: 0
  }
  followersData = FOLLOWERS_DATA
  recommendData = RECOMMEND_DATA
  hotData = HOT_DATA

  /**computed */
  get initialIndex(){
    return getArrayObjIndexByKey(this.tabLabels,{label:this.selectedLabel})
  }

  /**method */
  changePage(current: any) {
    this.selectedLabel = this.tabLabels[current].label
    console.log(current)
  }
  scroll(pos: any) {
    const x = Math.abs(pos.x)
    const tabItemWidth = (this.$refs.tabNav as any).$el.clientWidth
    const slideScrollerWidth = (this.$refs.slide as any).slide.scrollerWidth;
    const deltaX = x / slideScrollerWidth * tabItemWidth;
    (this.$refs.tabNav as any).setSliderTransform(deltaX)
  }
  resolveTitle(item: any) {
    return `${item.name}关注了问题 · ${item.postTime} 小时前`
  }
  resolveQuestionFollowers(item: any) {
    return `${item.answers} 赞同 · ${item.followers} 评论`
  }
}
